<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <!-- <van-notice-bar
        left-icon="volume-o"
        text="婚假（5天）、产假（4个月）、陪产假（5天）、丧假（5天）为带薪假期，不视为请假，超过标准天数的按事假处理；"
      /> -->

      <!-- 循环列表 begin -->
      <div v-for="(item,index) of qjList.qjmxes" :key="index" class="content">
        <div class="common-main box">
          <van-cell title="申请人" :value="qjList.sqrmc" />
          <van-cell title="请假类型" :value="qjlxMap[item.qjlx]" />
          <van-cell v-if="item.qjlx==='TX'" title="加班时间" :value="item.jbrq" />
          <van-cell v-if="item.qjlx==='PKJ'" title="考试类型" :value="kslxMap[item.kslx]" />
          <van-cell title="抄送人" :value="qjList.xmfzrmc" />
          <van-cell title="开始日期" :value="formDate(item.qjksrq)" />
          <van-cell title="开始时段" :value="item.qjkssjd===1?'上午':'下午'" />
          <van-cell title="结束日期" :value="formDate(item.qjjsrq)" />
          <van-cell title="结束时段" :value="item.qjjssjd===1?'上午':'下午'" />
        </div>
      </div>
      <!-- 循环列表 end -->
      <div class="content">
        <div class="common-main box">
          <van-field
            v-model="qjyy"
            rows="1"
            autosize
            label="请假原因"
            readonly
            type="textarea"
            input-align="right"
            placeholder="请输入请假原因"
          />
        </div>
      </div>
      <div class="common-space" />
      <!-- 底部按钮 begin -->
      <div v-if="spzt===1" class="foot-btn fixed">
        <ul>
          <li><van-button type="info" round size="large" @click="revokeQjxx(2)">撤销</van-button></li>
        </ul>
      </div>
    <!-- 底部按钮 end -->
    </div>
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
export default {
  name: 'QjxxXq',
  components: {
    headerBar
  },
  data() {
    return {
      headerData: {
        title: '请假信息详情',
        leftUrl: 'back',
        leftText: ' '
      },
      qjxx: '',
      qjList: [],
      id: '',
      qjyy: '',
      kslxMap: {
        1: '高考',
        0: '中考'
      },
      qjlxMap: {
        'BSJ': '事假',
        'NJ': '年假',
        'HJ': '婚假',
        'Cj': '产假',
        'PCJ': '陪产假',
        'SJ': '丧假',
        'TX': '调休',
        'PKJ': '陪考假'
      },
      spzt: ''
    }
  },
  created() {
    console.log('this.$route.params.qjxx=====================================================', this.$route.params.qjxx)
    this.id = this.$route.params.qjxx.id
    this.spzt = this.$route.params.qjxx.zt
    this.getQjxx()
  },
  methods: {
    revokeQjxx(qjxx) {
      this.$store.dispatch('erp/revokeQjxxAction', {
        id: this.id
      }).then(res => {
        this.$toast('撤销成功！')
        this.$router.back()
      }).catch(error => [
        this.$toast(error)
      ])
    },
    getQjxx() {
      this.$store.dispatch('erp/getQjxxByIdAction', {
        id: this.id
      }).then(res => {
        this.qjyy = res.qjyy
        this.qjList = res
        console.log('this.qjList======================================================', this.qjList)
      })
    },
    formDate(date) {
      date = date + ''
      return date.slice(0, 4) + '-' + date.slice(4, 6) + '-' + date.slice(6, 8)
    }
  }
}
</script>

<style scoped>
.holidays-flex{padding: 0 5px;}
.van-cell::after{display: none;}
</style>
